import React from "react";
import { Layout } from "src/components/ui/layout";
import { Product } from "src/components/product";
import { graphql } from "gatsby";
import { MainProductsLayout } from "src/components/ui/main-products-layout";

const WallMount = ({ data }) => {
  const {
    allWallMountsJson: { nodes: products },
  } = data;

  return (
    <MainProductsLayout heading="Кронштейны">
      {products.map((product) => (
        <Product key={product.id} product={product} />
      ))}
    </MainProductsLayout>
  );
};

WallMount.Layout = Layout;
export default WallMount;

export const query = graphql`
  {
    allWallMountsJson {
      nodes {
        sku
        images {
          childImageSharp {
            gatsbyImageData
          }
        }
        name
        price
        specs
        description
        id
        additionalInfo
      }
    }
  }
`;
